.sw_nav{display:flex;width:100%;position:relative;overflow:hidden;overflow-x:auto;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-overflow-scrolling:touch;-webkit-tap-highlight-color:transparent}.sw_nav::-webkit-scrollbar{background-color:transparent}.sw_nav div{flex:1 0 0;height:40px;line-height:40px}.sw_nav div.scrollable{flex:0 0 22%}.sw_nav div.disabled{color:#c8c9cc;cursor:not-allowed}.sw_nav .sw_line{position:absolute;height:3px;bottom:0;left:0;border-radius:1.5px;background-color:#409EFF;transition:all .2s}
